import {mapGetters, mapState} from 'vuex';
import uMask from '../../../components/basic-component/u-mask/u-mask.vue';
import appIphoneX from '../../../components/basic-component/app-iphone-x/app-iphone-x.vue';
import appRadio from '../../../components/basic-component/app-radio/app-radio.vue';

export default {
    name: "index",
    data() {
        return {
            first: true,
            loading: false,
            code: '',
            dialog: false,
            msg: '',
            other: '',
            r_raffled_at: '',
            list: [],
            mode: 0,
            exchange: 0,
            token: ''
        };
    },
    components:{
        'app-radio': appRadio,
        uMask,
        'app-iphone-x': appIphoneX,
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.code = options.code;
        that.exchange = options.exchange == 1 ? 1 : 0;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getList();
    },
    onShow() {
        if(this.exchange == 1 && !this.first) {
            this.getList()
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            exchangeImg: state => state.mallConfig.__wxapp_img.exchange
        })
    },
    methods: {
        toDetail(item) {
            if(item.type == 'goods') {
                uni.navigateTo({
                    url: `/pages/goods/goods?id=${item.goods_id}&code=${this.code}&token=${item.token}&attr=${item.goods_info.attr_str}&attr_id=${item.attr_id}&goods_num=${item.goods_num}&exchange=${item.is_send}`
                });
            }else if(item.type == 'coupon') {
                uni.navigateTo({
                    url: `/pages/coupon/details/details-no-share?person=1&id=${item.user_coupon_id[0]}`
                });
            }else if(item.type == 'card') {
                uni.navigateTo({
                    url: `/pages/card/details/details?id=${item.user_card_id[0]}`
                });
            }
        },
        goback() {
            this.dialog = false;
            uni.navigateBack();
        },
        apply(item) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '领取中...'
            });
            if(item.type != 'goods') {
                that.$request({
                    url: that.$api.exchange.covert,
                    data: {
                        code: this.code,
                        token: item.token
                    },
                    method: 'post'
                }).then(response=>{
                    uni.hideLoading();
                    if(response.code == 0) {
                        uni.showToast({
                            title: response.msg,
                            duration: 1000
                        });
                        this.getList();
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                });
            }else {
                let mch_list = [{
                    mch_id: 0,
                    goods_list: [{
                        id: item.goods_id,
                        attr: item.goods_info.attr_str,
                        num: item.goods_num,
                        cat_id: 0,
                        goods_attr_id: item.attr_id
                    }],
                    code: this.code,
                    token: item.token,
                }];
                let url = `/pages/order-submit/order-submit?mch_list=${JSON.stringify(mch_list)}`;
                url += `&preview_url=${encodeURIComponent(this.$api.exchange.exchange_preview)}&submit_url=${encodeURIComponent(this.$api.exchange.exchange_submit)}&plugin=exchange`;
                uni.navigateTo({
                    url: url
                })
            }
        },
        toggle(e) {
            for(let item of this.list) {
                if(item.token == e.item.token) {
                    item.active = e.active;
                    this.token = e.item.token
                }else {
                    item.active = false;
                }
            }
            this.$forceUpdate();
        },
        submit() {
            let that = this;
            if(that.mode == 1 && !that.token) {
                uni.showToast({
                    title: '请选择想要兑换的礼品',
                    icon: 'none',
                    duration: 1000
                });
                return false
            }
            if(this.loading) {
                return false
            }
            this.loading = true;
            uni.showLoading({
                mask: true,
                title: '领取中...'
            });
            that.$request({
                url: that.$api.exchange.unite,
                data: {
                    code: this.code,
                    token: this.token
                },
                method: 'post'
            }).then(response=>{
                this.loading = false;
                uni.hideLoading();
                if(response.code == 0) {
                    this.exchange = 1;
                    this.mode = 0;
                    this.getList();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.exchange == 1 ? that.$api.exchange.log_detail : that.$api.exchange.info,
                data: {
                    code: that.code
                }
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                that.first = false;
                if(response.code == 0) {
                    if(that.exchange == 0) {
                        that.list = response.list.rewards;
                        that.mode = response.list.mode;
                        if(that.mode == 1) {
                            for(let item of that.list) {
                                item.active = false;
                            }
                        }
                    }else {
                        that.r_raffled_at = response.data.codeModel.r_raffled_at
                        that.list = response.data.rewards;
                    }
                }else {
                    if(that.exchange == 0) {
                        that.msg = response.msg;
                        that.other = '';
                        if(response.msg == '该兑换码未到使用时间!') {
                            that.other = response.data.valid_start_time + '-' + response.data.valid_end_time + ' 可用'
                        }
                        that.dialog = true;
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        }
    }
}
